.header {
  height: 6rem;
  background-color: var(--elements);
  box-shadow: 0 0.3rem 0.7rem var(--shadow);
}

.header div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.header a {
  font-size: 2rem;
  font-weight: bold;
  color: var(--text);
  text-decoration: none;
}

.header button {
  cursor: pointer;
  background-color: transparent;
  border: none;
  color: var(--text);
}

.header button span {
  font-size: 1.7rem;
  margin-left: 0.8rem;
}

@media (max-width: 576px) {
  .header a {
    font-size: 1.7rem;
  }
  .header button span {
    font-size: 1.4rem;
  }
}
